.container {
  height: 100%;
  width: 100%; }

@keyframes blink-animation {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.2; }
  100% {
    opacity: 1; } }

.header {
  height: 70vh;
  width: 100%;
  background-color: #34415C;
  display: flex;
  align-items: flex-end;
  justify-content: center; }
  .header .logo {
    position: absolute;
    width: 100%;
    height: 70%;
    background-position-x: left;
    background-position-y: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    pointer-events: none; }
    .header .logo.loading {
      animation: blink-animation 2s ease-in-out infinite; }
  .header .content {
    z-index: 10;
    width: 100%;
    max-width: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; }
    .header .content .title {
      font-size: 3em; }
    .header .content .subtitle {
      margin-bottom: 60px;
      margin-top: 10px;
      font-size: 1em;
      text-align: center;
      max-width: 380px; }
    .header .content input {
      width: 100%;
      color: #fff;
      font-size: 25px;
      background-color: transparent;
      border: none;
      margin-bottom: -1px;
      border-bottom: 2px solid #fff; }

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #222E49; }

.loader {
  margin-top: 50px;
  font-size: 20px; }

.dismiss {
  position: absolute;
  cursor: pointer;
  margin-left: 275px;
  margin-top: 80px; }

.list .content {
  display: flex;
  flex-wrap: wrap;
  max-width: 620px;
  margin-top: 35px;
  margin-bottom: 100px; }
  .list .content .repo {
    color: #fff;
    padding: 10px;
    border: 1px solid rgba(255, 255, 255, 0.38);
    border-radius: 10px;
    margin: 10px;
    width: 164px; }
    .list .content .repo:hover {
      border: 1px solid #fff; }
    .list .content .repo .name {
      font-size: 20px;
      word-wrap: break-word; }
    .list .content .repo .score {
      font-size: 15px;
      padding-top: 8px; }



